.page-container {
  position: relative;
  display: block;
}

.page-header {
  position: absolute;
  width: calc(100% - 280px);
  margin: 0 30px;
  top: 0;
  right: 0;
  z-index: 999;
  -webkit-transition: all 0.2s ease-in-out;
  -moz-transition: all 0.2s ease-in-out;
  -o-transition: all 0.2s ease-in-out;
  transition: all 0.2s ease-in-out;
}

.header-title h5 {
  margin: 0;
  line-height: 41px;
  color: #fff;
}

.header-search {
  background-color: #181821;
  border-radius: 10px;
}

.header-search:before {
  display: block;
  content: "search";
  font-family: "Material Icons";
  font-size: 20px;
  height: 42px;
  line-height: 42px;
  padding: 0 15px;
  position: absolute;
  z-index: 99;
  color: #9a9cab;
}

.header-search .form-control {
  margin-left: 30px;
  border: none;
  background-color: #181821;
  width: calc(100% - 30px);
  color: #fff;
}

.header-search .form-control::placeholder {
  color: #9a9cab;
}

.header-search .form-control:focus {
  outline: 0;
  box-shadow: none;
}

.header-search .close-search {
  display: none;
  position: absolute;
  right: 15px;
  top: 50%;
  transform: translateY(-50%);
  color: #9a9cab;
}

.header-search .close-search svg {
  width: 20px;
  height: 20px;
}

.page-header .navbar {
  padding: 14px 30px;
  display: flex !important;
  border-bottom: 1px solid #262635;
}

.page-header .navbar .logo {
  display: flex !important;
  flex: 2;
}

.page-header .navbar #activity-sidebar-toggle {
  background: #1f1f2b;
  line-height: 22px;
  border-radius: 5px;
  min-width: 40px;
  text-align: center;
  font-weight: 700;
  box-shadow: 0 1px 3px 0 rgb(54 74 99 / 5%);
}

.page-header .navbar #activity-sidebar-toggle svg {
  color: #fff;
  width: 18px !important;
  height: 18px !important;
}

.page-header .navbar a#sidebar-toggle {
  padding: 9px 0;
  margin-right: 20px;
  float: left;
  transform: rotate(0);
  display: none;
  -webkit-transition: all 0.2s ease-in-out;
  -moz-transition: all 0.2s ease-in-out;
  -o-transition: all 0.2s ease-in-out;
  transition: all 0.2s ease-in-out;
}

.page-header .navbar a#sidebar-toggle:hover {
  background: 0 0;
}

.page-header .navbar #headerNav,
.page-header .navbar #navbarNav {
  display: flex !important;
  flex: 10;
}

.page-header .navbar #headerNav .navbar-nav,
.page-header .navbar #navbarNav .navbar-nav {
  flex-direction: row !important;
}

.page-header #sidebar-toggle svg {
  color: #9a9cab;
  width: 22px;
  height: 22px;
}

.page-header .navbar #headerNav>ul>li>a {
  padding: 9px 0;
  margin: 0 7px;
  color: #9a9cab;
  -webkit-transition: all 0.2s ease-in-out;
  -moz-transition: all 0.2s ease-in-out;
  -o-transition: all 0.2s ease-in-out;
  transition: all 0.2s ease-in-out;
}

.page-header .navbar #headerNav>ul>li>a svg {
  line-height: 22px;
  vertical-align: middle;
  width: 21px;
  height: 21px;
}

.page-header .navbar #headerNav>ul>li>a.notifications-dropdown {
  padding: 9px 15px;
  background: #1f1f2b;
  color: #fff;
  line-height: 22px;
  border-radius: 5px;
  min-width: 40px;
  text-align: center;
  font-weight: 700;
  box-shadow: 0 1px 3px 0 rgb(54 74 99 / 5%);
}

.page-header .navbar #headerNav>ul>li>a.notifications-dropdown .spinner-grow {
  width: 8px;
  height: 8px;
  right: 16px;
  top: 9px;
  position: absolute;
  -webkit-animation: 2.4s linear infinite spinner-grow;
  animation: 2.4s linear infinite spinner-grow;
}

.page-header .navbar #headerNav>ul>li>a.profile-dropdown {
  padding: 0;
  margin-right: 0;
}

.page-header .navbar #headerNav>ul>li>a img {
  width: 40px;
  box-shadow: 0 1px 3px 0 rgb(54 74 99 / 5%);
  border-radius: 5px;
}

.page-header .navbar .navbar-nav {
  flex: 10;
  padding: 0;
}

.navbar-nav .activity-trigger svg {
  color: #9a9cab;
}

.page-header #headerNav {
  flex: 10;
  align-items: flex-end;
  flex-direction: column;
}

.page-header .header-notif {
  padding: 13px 26px;
  -webkit-transition: all 0.1s ease-in-out;
  -moz-transition: all 0.1s ease-in-out;
  -o-transition: all 0.1s ease-in-out;
  transition: all 0.1s ease-in-out;
}

.page-header .header-notif:hover .notif-text {
  color: #fff;
  -webkit-transition: all 0.1s ease-in-out;
  -moz-transition: all 0.1s ease-in-out;
  -o-transition: all 0.1s ease-in-out;
  transition: all 0.1s ease-in-out;
}

.page-header .header-notif .notif-image {
  float: left;
  margin-right: 10px;
  margin-top: 3px;
  border-radius: 50%;
}

.page-header .header-notif .notif-image span {
  width: 40px;
  height: 40px;
  display: block;
  line-height: 40px;
  text-align: center;
  border-radius: 5px;
  display: inline-block;
}

.page-header .header-notif .notif-image img {
  width: 40px;
  height: 40px;
  border-radius: 5px;
}

.page-header .header-notif .notif-text {
  width: 223px;
  display: block;
  padding: 0 5px;
  display: inline-block;
  color: #9a9cab;
  font-weight: 400;
}

.page-header .header-notif .notif-text .bold-notif-text {
  font-weight: 500;
}

.page-header .header-notif .notif-text p {
  margin: 0;
}

.page-header .header-notif .notif-text small {
  opacity: 0.7;
}

.profile-drop-menu a span {
  margin-left: 25px;
}

.search-drop-menu {
  width: 340px;
}

.notif-drop-menu {
  width: 340px;
  max-height: 420px;
  overflow-y: auto;
}

.notif-drop-menu .dropdown-header {
  margin-bottom: 5px;
  font-size: 14px;
}

.profile-drop-menu a svg {
  margin-right: 10px;
  width: 19px;
  height: 19px;
}

.navbar-nav .dropdown-menu {
  position: absolute !important;
  visibility: hidden;
  opacity: 0;
  margin-top: 30px;
  display: block;
  -webkit-transition: all 0.2s ease-in-out;
  -moz-transition: all 0.2s ease-in-out;
  -o-transition: all 0.2s ease-in-out;
  transition: all 0.2s ease-in-out;
}

.navbar-nav .dropdown-menu.show {
  margin-top: 20px;
  opacity: 1;
  visibility: visible;
}

.slimScrollBar {
  background: rgba(0, 0, 0, 0.2) !important;
  border-radius: 4px !important;
}

.page-sidebar {
  position: fixed;
  height: 100%;
  z-index: 9999;
  left: 0;
  background: #1f1f2b;
  box-shadow: 0 1px 3px 0 rgb(54 74 99 / 5%);
  top: 0;
  width: 220px;
  -webkit-transition: all 0.2s ease-in-out;
  -moz-transition: all 0.2s ease-in-out;
  -o-transition: all 0.2s ease-in-out;
  transition: all 0.2s ease-in-out;
  transform: scale(1, 1);
  transform-origin: left;
  /* overflow-x: scroll; */
  /* overflow: visible; */
}

.page-sidebar .ps__rail-y {
  margin-top: 8px !important;
  margin-bottom: 8px !important;
  background-color: transparent !important;
}

.page-sidebar .accordion-menu {
  margin-top: 20px;
  height: calc(100% - 141px) !important;
  padding: 10px 15px;
}

.page-sidebar .accordion-menu>li>a {
  display: block;
  color: #9a9cab;
  -webkit-transition: all 0.1s ease-in-out;
  -moz-transition: all 0.1s ease-in-out;
  -o-transition: all 0.1s ease-in-out;
  transition: all 0.1s ease-in-out;
  line-height: 45px;
  padding: 5px 15px;
  text-decoration: none;
}

.page-sidebar .accordion-menu>li.active-page>a {
  color: #83d8ae;
  font-weight: 500;
}

.page-sidebar .accordion-menu>li.active-page>a>svg {
  color: #83d8ae !important;
}

.page-sidebar .accordion-menu>li.active-page ul li a.active {
  color: #fff;
}

.page-sidebar .accordion-menu>li>a:hover svg {
  margin-left: 5px;
}

.page-sidebar .accordion-menu>li>a>svg {
  width: 21px;
  height: 21px;
  line-height: 40px;
  text-align: center;
  vertical-align: text-top;
  color: #9a9cab;
  margin-right: 15px;
  -webkit-transition: all 0.2s ease-in-out;
  -moz-transition: all 0.2s ease-in-out;
  -o-transition: all 0.2s ease-in-out;
  transition: all 0.2s ease-in-out;
}

.page-sidebar .accordion-menu li.sidebar-title {
  font-weight: 500;
  padding: 10px 15px;
  font-size: 0.875rem;
  color: #6c757d;
  opacity: 0.8;
}

.page-sidebar .accordion-menu li a .dropdown-icon {
  float: right;
  vertical-align: middle;
  line-height: 44px;
  font-size: 10px;
  -webkit-transition: all 0.2s ease-in-out;
  -moz-transition: all 0.2s ease-in-out;
  -o-transition: all 0.2s ease-in-out;
  transition: all 0.2s ease-in-out;
}

.page-sidebar .accordion-menu li.open>a>.dropdown-icon {
  visibility: visible;
  transform: rotate(90deg);
}

.page-sidebar .accordion-menu li ul {
  padding: 5px 0;
  list-style: none;
}

.page-sidebar .accordion-menu li ul li a {
  color: #9a9cab;
  display: block;
  padding: 5px 15px;
  font-size: 14px;
  position: relative;
  -webkit-transition: all 0.15s ease-in-out;
  -moz-transition: all 0.15s ease-in-out;
  -o-transition: all 0.15s ease-in-out;
  transition: all 0.15s ease-in-out;
  text-decoration: none;
}

.page-sidebar .accordion-menu li ul li a:hover {
  margin-left: 5px;
}

.page-sidebar .accordion-menu li ul li a i {
  font-size: 10px;
  padding-right: 21px;
  padding-left: 6px;
}

.page-sidebar .logo {
  margin-right: 0;
  width: auto;
  height: 30px;
  margin: 18px auto;
  display: block;
  text-align: center;
  font-size: 30px;
  font-weight: 500;
}

@media (min-width: 1350px) {
  .page-sidebar-collapsed .page-sidebar {
    width: 80px;
    /* overflow: visible !important; */
    overflow-x: scroll !important;
    border-radius: 0;
  }

  .page-sidebar-collapsed .logo {
    font-size: 0;
  }

  .page-sidebar-collapsed .logo::first-letter {
    font-size: 20px;
  }

  .page-sidebar-collapsed .page-content {
    margin-left: 80px;
  }

  .page-sidebar-collapsed .page-header {
    width: calc(100% - 160px);
  }

  .page-sidebar-collapsed .page-sidebar .accordion-menu {
    padding: 0;
    overflow: visible;
    position: absolute !important;
    /* height: auto !important; */
    top: 50%;
    /* top: 50%; */
    transform: translateY(-50%);
    margin-top: 0;
  }

  .page-sidebar-collapsed .page-sidebar .accordion-menu>li>a {
    font-size: 0;
  }

  .page-sidebar-collapsed .page-sidebar .accordion-menu>li>a {
    text-align: center;
    padding: 8px;
    width: 80px;
  }

  .page-sidebar-collapsed .page-sidebar .accordion-menu>li>a>.dropdown-icon {
    display: none;
  }

  .page-sidebar-collapsed .page-sidebar .accordion-menu>li>a>svg {
    margin: 0;
    vertical-align: middle;
  }

  .page-sidebar-collapsed .page-sidebar .accordion-menu>li {
    position: relative;
  }

  .page-sidebar-collapsed .page-sidebar .accordion-menu>li>a+ul {
    display: block !important;
    position: absolute;
    margin-left: 0;
    top: -15px;
    left: 110px;
    padding: 20px 0;
    background: #1f1f2b;
    box-shadow: 0 0 11px 1px rgba(0, 0, 0, 0.05);
    -webkit-box-shadow: 0 0 11px 1px rgba(0, 0, 0, 0.05);
    -moz-box-shadow: 0 0 11px 1px rgba(0, 0, 0, 0.05);
    min-width: 200px;
    opacity: 0;
    visibility: hidden;
    border-radius: 15px;
    -webkit-transition: all 0.2s ease-in-out;
    -moz-transition: all 0.2s ease-in-out;
    -o-transition: all 0.2s ease-in-out;
    transition: all 0.2s ease-in-out;
